import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';

import { Card, Row, Table, Popconfirm, Col, message } from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import BtnAddNew from 'components/common/button/BtnAddNew';

import { getDistricts, deleteDistrict } from 'services/district';

import './style.scss';

class District extends Component {
  constructor(props) {
    super(props);
    this.handleDelete = this.handleDelete.bind(this);
    this.state = {
      districts: [],
      loading: true,
    };
    this.title = 'Danh mục Huyện';
    this.urlEdit = '#/district/form/edit/';
    this.urlOfBtnAdd = '#/district/form/new/';
    this.columns = [
      {
        title: 'Ký hiệu huyện',
        dataIndex: 'id',
        key: 'id',
        // sorter: (a, b) => a.townId.length - b.townId.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Tên huyện huyện',
        dataIndex: 'label',
        key: 'label',
        // sorter: (a, b) => a.label.length - b.label.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Tên tiếng anh',
        dataIndex: 'labelInEngLish',
        key: 'labelInEngLish',
        // sorter: (a, b) => a.labelInEngLish.length - b.labelInEngLish.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          this.state.districts.length >= 1 ? (
            <Fragment>
              <BtnEdit href={`${this.urlEdit}${record.id}`} />
              <Popconfirm
                title="Sure to delete?"
                okText="Yes"
                cancelText="No"
                onConfirm={() => this.handleDelete(record.id)}
              >
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
    // rowSelection object indicates the need for row selection
    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
    this.cssBetweenRow = { marginBottom: '8px' };
  }

  componentDidMount = async () => {
    this.getDistricts();
  };

  onChange = (value, selectedOptions) => {
    console.log(value, selectedOptions);
  };

  getDistricts = async () => {
    const res = await getDistricts();
    this.setState({ districts: res.data.items, loading: false });
  };

  handleDelete = id => {
    deleteDistrict(id).then(res => {
      if (res.status === 200) {
        message.success('Xóa huyện thành công.');
        this.getDistricts();
      } else {
        message.error(res.message);
      }
    });
  };

  render() {
    return (
      <Card title={this.title}>
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/district/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              loading={this.state.loading}
              rowSelection={this.rowSelection}
              bordered
              dataSource={this.state.districts}
              columns={this.columns}
              onRow={() => ({
                onDoubleClick: () => {},
              })}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}
export default withRouter(props => (
  <Page {...props}>
    <District {...props} />
  </Page>
));



